.projects-slider__shadow {
  position: absolute;
  top: 0;
  z-index: 2;
  height: 100%;
  display: flex;
  width: 20%;
  justify-content: flex-start;
  background: linear-gradient(91.97deg, #00080F 1.1%, rgba(0, 8, 15, 0) 97.8%);
}

.projects-slider__shadow_right {
  right: 0;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.projects-slider__arrow {
  width: 48px;
  height: 48px;
  display: flex !important;
  align-items: center;
  justify-content: center;
  align-self: center;
  cursor: pointer;
  z-index: 1;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #FFFFFF;
  border-radius: 100px;
}

.projects-slider__arrow_next {
  transform: scaleX(-1);
}

.projects-slider__arrow-icon {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../../../images/arrow-next.svg);
  width: 5px;
  height: 10px;
}

@media screen and (max-width: 992px) {
  .projects-slider__shadow {
    display: none;
  }
}