.technologies {
  margin: 0 auto;
  padding: 148px 0 173px;
  max-width: 1240px;
  display: flex;
  flex-direction: column;
  align-items: center;
  scroll-margin-top: 130px;
}

.technologies__title {
  font-family: 'Century Gothic';
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 48px;
  line-height: 59px;
  color: #FFFFFF;
  text-shadow: 0px 0px 10px rgba(193, 255, 92, 0.2);
}

.technologies__subtitle {
  margin-top: 0;
  margin-bottom: 32px;
  max-width: 739px;
  font-weight: 400;
  text-align: center;
  font-size: 18px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.8);
}

.technologies__table {
  padding: 0;
  margin-top: 0;
  margin-bottom: 32px;
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: repeat(3, auto);
}

.technologies__table-cell {
  padding: 32px 16px;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: inset 0px 0px 40px rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 4px;
  grid-column: span 2;
}

.technologies__table-cell_last-row {
  grid-column: span 3;
}

.technologies__table-title {
  margin-top: 0;
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: #FFFFFF;
}

.technologies__table-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: rgba(255, 255, 255, 0.8)
}

.technologies__button {
  padding: 24px 48px;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #42C6FF;
  background: rgba(66, 198, 255, 0.1);
  border: 1px solid #42C6FF;
  border-radius: 100px;
  cursor: pointer;
}

.technologies__button:hover {
  background: rgba(66, 198, 255, 0.3);
}

.technologies__button:focus {
  background: rgba(66, 198, 255, 0.3);
}

.technologies__button:active {
  background: rgba(66, 198, 255, 0.5);
}

@media screen and (max-width: 1400px) {
  .technologies {
    max-width: 1024px;
  }
}

@media screen and (max-width: 1200px) {
  .technologies {
    padding-right: 60px;
    padding-left: 60px;
  }
}

@media screen and (max-width: 992px) {
  .technologies {
    padding-top: 120px;
    padding-bottom: 96px;
  }
}

@media screen and (max-width: 768px) {
  .technologies {
    padding-right: 15px;
    padding-left: 15px;
    scroll-margin-top: 60px;
  }

  .technologies__table {
    display: flex;
    flex-direction: column;
  }

  .technologies__table-cell {
    padding: 16px;
  }

  .technologies__button {
    align-self: stretch;
  }
}

@media screen and (max-width: 576px) {
  .technologies__title {
    font-size: 32px;
    line-height: 39px;
  }

  .technologies__subtitle {
    font-size: 16px;
    line-height: 21px;
  }

  .technologies__table-title {
    font-size: 16px;
    line-height: 21px;
  }

  .technologies__table-subtitle {
    font-size: 12px;
    line-height: 16px;
  }

  .technologies__button {
    padding-top: 16px;
    padding-bottom: 16px;
    font-size: 18px;
    line-height: 24px;
  }
}