.privacy-policy  {
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: .5s;
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  z-index: 3;
}

.privacy-policy_opened {
  visibility: visible;
  opacity: 1;
  transition: .5s;
}

.privacy-policy__container {
  width: 800px;
  padding: 48px;
  background: #00080F;
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.privacy-policy__header-container {
  margin-bottom: 24px;
  padding-bottom: 8px;
  align-self: stretch;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}

.privacy-policy__title {
  margin: 0;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #FFFFFF;
}

.privacy-policy__button_close {
  border: none;
  background: none;
  cursor: pointer;
  width: 32px;
  height: 32px;
  background-image: url(../../images/button-close.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.privacy-policy__text-container {
  margin-bottom: 24px;
  position: relative;
}

.privacy-policy__text {
  max-height: 683px;
  overflow: auto;
}

