.privacy-policy__fade {
  position: absolute; bottom: 0; width: 100%; height: 60px;
  background: linear-gradient(180deg, rgba(0, 8, 15, 0) 0%, #00080F 100%);
}

.privacy-policy__paragraph {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: rgba(255, 255, 255, 0.8);
}

.privacy-policy__text:last-of-type {
  margin-bottom: 0;
}

.privacy-policy__text::-webkit-scrollbar {
  width: 4px;
  background: rgba(52, 206, 255, 0.15);
  border-radius: 2px;
}

.privacy-policy__text::-webkit-scrollbar-thumb {
  width: 2px;
  background: #34CEFF;
  border-radius: 2px;
}

.privacy-policy__button-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-self: stretch;
  gap: 16px;
}

.privacy-policy__button {
  padding-top: 16px;
  padding-bottom: 16px;
  cursor: pointer;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  border-radius: 100px;
}

.privacy-policy__button_cancel {
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #FFFFFF;
}

.privacy-policy__button_submit {
  color: #34CEFF;
  background: rgba(52, 206, 255, 0.1);
  border: 1px solid #34CEFF;
}