.mobile-menu {
  background: #00080F;;
  padding: 0;
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

.mobile-menu_invisible {
  display: none;
}

.mobile-menu__button-close {
  align-self: flex-end;
  padding: 0;
  border: none;
  background: none;
  background-position: center;
  background-repeat: no-repeat;
  margin-top: 40px;
  margin-right: 40px;
  background-image: url(../../images/button-close.svg);
  width: 32px;
  height: 32px;
  cursor: pointer;
}

.mobile-menu__nav {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 28px;
  flex: 1 1;
  justify-content: center;
}

.mobile-menu__item {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu__link {
  padding: 0;
  border: none;
  background: none;
  list-style: none;
  font-weight: 400;
  font-size: 22px;
  line-height: 29px;
  color: #FFFFFF;
}