.footer {
  margin: 0 auto;
  padding-bottom: 39px;
  margin-top: 212px;
  max-width: 1240px;
  display: flex;
  justify-content: space-between;
}

.footer__logo-container {
  display: flex;
  gap: 12px;
  align-items: center;
}

.footer__logo-title {
  margin: 0;
  font-family: 'Orbitron';
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #FFFFFF;
}

.footer__logo-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8)
}

.footer__contacts {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 32px;
}

.footer__contact {
  list-style: none;
}

.footer__contact-link {
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #42C6FF;
  display: flex;
  gap: 6px;
  align-items: center;
}

.footer__contact-icon {
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/TelegramLogo-blue.svg);
}

.footer__link {
  border: none;
  padding: 0;
  background: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}

@media screen and (max-width: 1400px) {
  .footer {
    max-width: 1024px;
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    margin-top: 120px;
    padding-right: 60px;
    padding-left: 60px;
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding-bottom: 47px;
    margin-top: 61px;
    flex-direction: column;
    align-items: center;
    gap: 32px;
  }

  .footer__logo-container {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto auto;
  }

  .footer__logo-subtitle {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding-right: 15px;
    padding-left: 15px;
  }
}