.project-team {
  margin: 0 auto;
  padding-bottom: 182px;
  max-width: 1240px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  scroll-margin-top: 130px;
}

.project-team__container {
  display: flex;
  flex-direction: column;
  gap: 48px;
}

.project-team__title {
  margin: 0;
  font-family: 'Century Gothic';
  font-weight: 700;
  font-size: 48px;
  line-height: 59px;
  text-shadow: 0px 0px 10px rgba(193, 255, 92, 0.2);
  color: #FFFFFF;
}

.project-team__subtitle {
  margin: 0;
  max-width: 526px;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: rgba(255, 255, 255, 0.8);
}

.project-team__span {
  color: rgba(66, 198, 255, 1);
}

.project-team__countries {
  padding: 0;
  margin: 0;
  display: flex;
  gap: 20px;
}

.project-team__country {
  list-style: none;
}

@media screen and (max-width: 1400px) {
  .project-team {
    max-width: 1024px;
  }

  .project-team__image {
    width: 40%;
  }
}

@media screen and (max-width: 1200px) {
  .project-team {
    padding-right: 60px;
    padding-left: 60px;
  }

  .project-team__image {
    width: 35%;
  }
}

@media screen and (max-width: 992px) {
  .project-team {
    flex-direction: column;
    gap: 40px;
    padding-bottom: 120px;
  }

  .project-team__container {
    align-items: center;
  }

  .project-team__subtitle {
    text-align: center;
  }
}

@media screen and (max-width: 768px) {
  .project-team {
    padding-right: 15px;
    padding-left: 15px;
    scroll-margin-top: 60px;
  }

  .project-team__container {
    gap: 24px;
  }
}

@media screen and (max-width: 576px) {
  .project-team__title {
    font-size: 32px;
    line-height: 39px;
  }

  .project-team__subtitle {
    font-size: 16px;
    line-height: 21px;
  }

  .project-team__countries {
    gap: 8px;
  }
}