.projects {
  margin: 0 auto;
  padding-bottom: 148px;
  max-width: 1240px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects__title {
  margin-top: 0;
  margin-bottom: 32px;
  font-weight: 700;
  font-size: 48px;
  line-height: 59px;
  font-family: 'Century Gothic';
  color: #FFFFFF;
  text-shadow: 0px 0px 10px rgba(193, 255, 92, 0.2);
}

@media screen and (max-width: 1400px) {
  .projects {
    max-width: 1024px;
  }
}

@media screen and (max-width: 1200px) {
  .projects {
    padding-right: 60px;
    padding-left: 60px;
  }
}

@media screen and (max-width: 768px) {
  .projects {
    padding-right: 15px;
    padding-left: 15px;
    padding-bottom: 113px;
  }
}

@media screen and (max-width: 576px) {
  .projects__title {
    font-size: 32px;
    line-height: 39px;
  }
}