.feedback-form {
  position: relative;
  margin: 0 auto;
  max-width: 1240px;
}

.feedback-form__wrapper {
  padding: 96px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../images/form-background.png);
  border: 1px solid rgba(52, 206, 255, 1);
  border-radius: 8px;
  position: relative;
  z-index: 1;
}

.feedback-form__object {
  position: absolute;

  background-position: center;
  background-repeat: no-repeat;
}

.feedback-form__object_left {
  background-image: url(../../images/Object3.png);
  width: 225px;
  height: 192px;
  left: -155px;
  bottom: -100px;
}

.feedback-form__object_right {
  background-image: url(../../images/Object4.png);
  width: 150px;
  height: 140px;
  z-index: 2;
  right: -60px;
  top: 25%;
}

.feedback-form__title {
  margin-top: 0;
  margin-bottom: 16px;
  font-family: 'Century Gothic';
  font-weight: 700;
  font-size: 48px;
  line-height: 59px;
  color: #FFFFFF;
  text-shadow: 0px 0px 10px rgba(193, 255, 92, 0.2)
}

.feedback-form__subtitle {
  margin-top: 0;
  margin-bottom: 48px;
  max-width: 830px;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
}

.feedback-form__form {
  align-self: stretch;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 16px;
}

.feedback-form__label {
  position: relative;
}

.feedback-form__input {
  width: calc(100% - 32px);
  margin: 0;
  padding: 16px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
}

.feedback-form__input:hover {
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.feedback-form__input:focus {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid #FFFFFF;
}

.feedback-form__input_invalid {
  border: 1px solid #F36060 !important;
}

.feedback-form__input::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
}

.feedback-form__delete-button {
  border: none;
  background-color: transparent;
  width: 22px;
  height: 22px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/cancel.svg);
  position: absolute;
  top: 16px;
  right: 16px;
  cursor: pointer;
}

.feedback-form__delete-button_invisible {
  display: none;
}

.feedback-form__delete-button:focus {
  outline: none;
}

.feedback-form__error {
  bottom: -15px;
  right: 0;
  position: absolute;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #F36060;
}

.feedback-form__error_agreement {
  left: 0;
}

.feedback-form__agreement {
  display: flex;
  gap: 12px;
  align-self: center;
  position: relative;
}

.feedback-form__checkbox_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.feedback-form__checkbox_visible {
  display: inline-block;
  vertical-align: middle;
  border: none;
  cursor: pointer;
}

.feedback-form__checkbox_invisible:checked + .feedback-form__checkbox_visible {
  background-image: url(../../images/checkbox.svg);
  background-position: center;
  background-repeat: no-repeat;
  width: 22px;
  height: 22px;
}

.feedback-form__checkbox_invisible:not(:checked) + .feedback-form__checkbox_visible {
  background-image: none;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 20px;
  height: 20px;
}

.feedback-form__checkbox_invisible:focus-visible+ .feedback-form__checkbox_visible {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.6);
  width: 20px;
  height: 20px;
}

.filter-checkbox__title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
}

.feedback-form__button {
  padding-top: 16px;
  padding-bottom: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #34CEFF;
  background: rgba(52, 206, 255, 0.1);
  border: 1px solid #34CEFF;
  border-radius: 100px;
  cursor: pointer;
}

.feedback-form__button:hover {
  background: rgba(66, 198, 255, 0.3);
}

.feedback-form__button:focus {
  background: rgba(66, 198, 255, 0.3);
}

.feedback-form__button:active {
  background: rgba(66, 198, 255, 0.5);
}

@media screen and (max-width: 1600px) {
  .feedback-form__object_left {
    left: -80px;
    bottom: -60px;
  }
}

@media screen and (max-width: 1400px) {
  .feedback-form {
    max-width: 1024px;
  }

  .feedback-form__object_left {
    bottom: -50px;
  }
}

@media screen and (max-width: 1200px) {
  .feedback-form {
    padding: 0 60px;
  }

  .feedback-form__object_left {
    left: 0;
  }

  .feedback-form__object_right {
    right: 0;
  }
}

@media screen and (max-width: 992px) {
  .feedback-form__wrapper {
    padding: 44px;
  }

  .feedback-form__form {
    display: flex;
    flex-direction: column;
  }

  .feedback-form__agreement {
    align-self: flex-start;
  }

  .feedback-form__object_right {
    right: -50px;
    top: -80px;
  }

  .feedback-form__object_left {
    left: -80px;
    bottom: -70px;
  }
}

@media screen and (max-width: 768px) {
  .feedback-form {
    padding: 0 15px;
  }

  .feedback-form__wrapper {
    padding: 24px;
  }
}

@media screen and (max-width: 576px) {
  .feedback-form__title {
    font-size: 24px;
    line-height: 29px;
    text-align: center;
  }

  .feedback-form__subtitle {
    margin-bottom: 24px;
    font-size: 16px;
    line-height: 21px;
  }

  .feedback-form__button {
    font-size: 16px;
    line-height: 21px;
  }
}