.slick-track {
  display: flex !important;
  gap: 32px;
  align-items: center;
}

.projects-slider {
  width: 100% !important;
  max-width: 1200px !important;
  margin: 0 auto !important;
}

.projects-slider__item {
  width: auto !important;
  padding: 24px;
  background: linear-gradient(247.79deg, rgba(255, 255, 255, 0.01) -5.02%, rgba(255, 255, 255, 0.005) 107.01%);
  backdrop-filter: blur(50px);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.025);
  display: flex !important;
  flex-direction: column;
  align-items: center;
  min-width: 268px !important
}

.projects-slider__item-img {
  width: 100%;
  object-fit: contain;
}

.projects-slider__item-title {
  margin: 24px 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
  color: #FFFFFF;
}

.projects-slider__item-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
}

.projects-slider__item-container {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}

.projects-slider__item-tags {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 4px;
}

.projects-slider__item-tag {
  padding: 4px 8px;
  background: #00080F;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  list-style: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.8);
}

.projects-slider__item-link {
  padding: 4px 8px;
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #34CEFF;
  background: #00080F;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

@media screen and (max-width: 1400px) {
  .projects-slider {
    max-width: 1024px !important;
  }
}

@media screen and (max-width: 1200px) {
  .projects-slider {
    width: calc(100vw - 120px) !important;
  }
}

@media screen and (max-width: 992px) {
  .slick-list {
    padding: 0 !important;
  }

  .slick-track {
    gap: 0;
  }

  .slick-active {
    margin-right: 12px;
  }
}

@media screen and (max-width: 768px) {
  .projects-slider {
    width: calc(100vw - 30px) !important;
  }
}

@media screen and (max-width: 576px) {
  .projects-slider__item {
    padding: 16px;
  }

  .projects-slider__item-title {
    margin: 16px 0;
    font-size: 16px;
    line-height: 21px;
  }

  .projects-slider__item-subtitle {
    font-size: 16px;
    line-height: 21px;
  }

  .projects-slider__item-container {
    margin-top: 16px;
    flex-direction: column;
    gap: 12px;
  }
}