.about {
  margin: 0 auto;
  padding-top: 266px;
  padding-bottom: 253px;
  max-width: 1240px;
  display: flex;
  position: relative;
}

.about__container {
  max-width: 692px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.about__logo {
  grid-area: logo;
  display: flex;
  align-items: center;
  gap: 24px;
}

.about__title {
  margin: 0;
  font-family: 'Century Gothic';
  font-weight: 700;
  font-size: 80px;
  line-height: 98px;
  color: #FFFFFF;
  text-shadow: 0px 0px 10px rgba(193, 255, 92, 0.2);
}

.about__subtitle {
  margin-top: 21.5px;
  margin-bottom: 56px;
  font-weight: 400;
  font-size: 32px;
  line-height: 42px;
  color: #FFFFFF;
  grid-area: subtitle;
}

.about__subtitle_blue {
  color: rgba(66, 198, 255, 1);
}

.about__button {
  padding: 24px 48px;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #42C6FF;
  background: rgba(66, 198, 255, 0.1);
  border: 1px solid #42C6FF;
  border-radius: 100px;
  cursor: pointer;
  grid-area: button;
}

.about__button:hover {
  background: rgba(66, 198, 255, 0.3);
}

.about__button:focus {
  background: rgba(66, 198, 255, 0.3);
}

.about__button:active {
  background: rgba(66, 198, 255, 0.5);
}

@keyframes objects {
  0% {
    background-image: url(../../images/Animation/1.png);
  }
  25% {
    background-image: url(../../images/Animation/2.png);
  }
  50% {
    background-image: url(../../images/Animation/3.png);
  }
  75% {
    background-image: url(../../images/Animation/4.png);
  }
  100% {
    background-image: url(../../images/Animation/5.png);
  }
}  

.about__image {
  animation: objects 3s infinite alternate-reverse;
  width: 585px;
  height: 645px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 142px;
  right: -114px;
}

@media screen and (max-width: 1600px) {
  .about__image {
    width: 485px;
    height: 545px;
    right: -40px;
  }
}

@media screen and (max-width: 1400px) {
  .about {
    max-width: 1024px;
  }

  .about__image {
    width: 385px;
    height: 445px;
    right: -40px;
  }
}

@media screen and (max-width: 1200px) {
  .about {
    padding-right: 60px;
    padding-left: 60px;
    padding-top: 180px;
    padding-bottom: 120px;
    flex-direction: column-reverse;
    gap: 79px;
    align-items: center;
  }

  .about__image {
    position: relative;
    right: 0;
    top: 0;
  }

  .about__container {
    align-items: center;
  }

  .project-logo {
    margin-right: 0;
  }

  .about__subtitle {
    text-align: center;
  }
}

@media screen and (max-width: 992px) {
  .about__button {
    align-self: stretch;
  }
}

@media screen and (max-width: 768px) {
  .about {
    padding-right: 15px;
    padding-left: 15px;
    padding-bottom: 81px;
  }

  .about__logo-image {
    width: 59px;
    height: 58px;
  }

  .about__image {
    width: 200px;
    height: 218px;
  }
}

@media screen and (max-width: 576px) {
  .about__container {
    align-self: stretch;
  }

  .about__title {
    font-size: 40px;
    line-height: 49px;
  }

  .about__subtitle {
    max-width: 330px;
    margin-top: 26px;
    font-size: 18px;
    line-height: 24px;
  }

  .about__button {
    padding-top: 16px;
    padding-bottom: 16px;
    font-size: 18px;
    line-height: 24px;
  }
}