.header {
  padding: 48px 0;
  position: fixed;
  width: 100vw;
  z-index: 3;
  backdrop-filter: blur(25px);
}

.header__wrapper {
  margin: 0 auto;
  max-width: 1240px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__links {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 48px;
}

.header__item {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header__link {
  padding: 0;
  border: none;
  background: none;
  list-style: none;
  font-weight: 400;
  font-size: 22px;
  line-height: 29px;
  color: rgba(255, 255, 255, 0.8);
}

.header__container {
  display: flex;
  align-items: center;
  gap: 24px;
}

.header__contact-img {
  width: 32px;
  height: 32px;
}

.header__select-language {
  border: none;
  background: none;
  display: flex;
  gap: 12px;
  align-items: center;
  cursor: pointer;
}

.header__select-language-value {
  margin: 0;
  font-weight: 400;
  font-size: 22px;
  line-height: 29px;
  color: rgba(255, 255, 255, 0.8);
}

.heder__mobile-menu {
  border: none;
  background: none;
  background-repeat: no-repeat;
  background-position: center;
  width: 24px;
  height: 24px;
  background-image: url(../../images/mobile-menu.svg);
  cursor: pointer;
}

@media screen and (max-width: 1400px) {
  .header__wrapper {
    max-width: 1024px;
  }
}

@media screen and (max-width: 1200px) {
  .header__wrapper {
    padding: 0 60px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .header__wrapper {
    padding: 0 15px;
  }

  .header__contact-img {
    width: 24px;
    height: 24px;
  }

  .header__select-language-icon {
    width: 22px;
    height: 16px;
  }

  .header__select-language-value {
    font-size: 16px;
    line-height: 21px;
  }
}