.service {
  position: relative;
  max-width: 1240px;
  margin: 0 auto;
  scroll-margin-top: 130px;
}

.service__wrapper {
  padding: 96px 112px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-position: initial;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../images/service-section-background.png);
  position: relative;
  z-index: 1;
}

.service__object {
  position: absolute;
  width: 120px;
  height: 120px;
  background-position: center;
  background-repeat: no-repeat;
}

.service__object_left {
  background-image: url(../../images/object1.png);
  top: -55px;
  left: -45px;
}

.service__object_right {
  background-image: url(../../images/object2.png);
  bottom: -85px;
  right: 25px;
  z-index: 2;
}

.service__title {
  margin-top: 0;
  margin-bottom: 70px;
  font-family: 'Century Gothic';
  font-weight: 700;
  font-size: 48px;
  line-height: 59px;
  color: #FFFFFF;
  text-shadow: 0px 0px 10px rgba(193, 255, 92, 0.2);
}

.service__table {
  margin-top: 0;
  margin-bottom: 64px;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 48px;
}

.service__table-item {
  list-style: none;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 32px;
}

.service__table-icon {
  width: 32px;
  height: 32px;
  background-position: center;
  background-repeat: no-repeat;
}

.service__table-icon_backend {
  background-image: url(../../images/backend.svg);
}

.service__table-icon_mobile-app {
  background-image: url(../../images/mobile-app.svg);
}

.service__table-icon_smart-conracts {
  background-image: url(../../images/smart-contracts.png);
}

.service__table-icon_ui-ux {
  background-image: url(../../images/ui-ux.svg);
}

.service__table-icon_crm-systems {
  background-image: url(../../images/crm-system.svg);
}

.service__table-text {
  max-width: 164px;
  text-align: center;
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.8)
}

.service__button {
  padding: 0;
  border: none;
  background: none;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #34CEFF;
  display: flex;
  gap: 12px;
  cursor: pointer;
}

.service__button-icon {
  width: 20px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/arrow-bottom.svg);
  transform: translateY(25%);
  -webkit-transform: translateY(25%);
}

@media screen and (max-width: 1400px) {
  .service {
    max-width: 1024px;
  }

  .service__wrapper {
    padding: 96px 60px;
  }
}

@media screen and (max-width: 1200px) {
  .service {
    padding: 0 60px;
  }

  .service__object_left {
    left: 0;
  }

  .service__object_right {
    right: 85px;
  }
}

@media screen and (max-width: 992px) {
  .service__title {
    margin-bottom: 37px;
  }

  .service__wrapper {
    padding: 48px;
  }
}

@media screen and (max-width: 768px) {
  .service {
    padding: 0 15px;
    scroll-margin-top: 60px;
  }

  .service__object_left {
    left: -40px;
    top: -70px;
  }

  .service__object_right {
    right: -20px;
  }

  .service__table {
    display: flex;
    flex-direction: column;
    gap: 24px;
    margin-bottom: 32px
  }

  .service__table-item {
    flex-direction: row
  }

  .service__table-text {
    text-align: start;
  }
}

@media screen and (max-width: 576px) {
  .service__title {
    font-size: 32px;
    line-height: 39px;
  }

  .service__table-text {
    font-size: 16px;
    line-height: 21px;
  }
}